<template>
  <Carousel @next="next" @previous="previous">
    <Slide
      v-for="(slide, index) in slides"
      :key="slide"
      :index="index"
      :visibleSlide="visibleSlide"
      :direction="direction"
    >
      <img :src="slide.img" />
    </Slide>
  </Carousel>
  <Article></Article>
  <section class="baseline">
    <h1>
      Lorem ipsum dolor sit amet consectetur adipisicing elit. Assumenda
