      <router-link to="/about" class="section_link">A propos</router-link>
    </h2>
  </section>
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
    <path
      fill="#273036"
      fill-opacity="1"
      d="M0,288L480,192L960,224L1440,224L1440,0L960,0L480,0L0,0Z"
    ></path>
  </svg>
  <Article></Article>
</template>

<script>
// @ is an alias to /src
import Carousel from "@/components/Carousel.vue";
import Slide from "@/components/Slide.vue";
import Article from "@/components/Article.vue";
export default {
  name: "Home",
  components: {
    Carousel,
    Slide,
    Article,
  },
  data() {
    return {
      slides: [
        {
          img: "https://images.unsplash.com/photo-1481349518771-20055b2a7b24?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8",
        },
        {
          img: "https://images.unsplash.com/photo-1509281373149-e957c6296406?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=100&q=80",
        },
        {
          img: "https://images.unsplash.com/photo-1509114397022-ed747cca3f65?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=100&q=80",
        },
      ],
      visibleSlide: 0,
      direction: "left",
    };
  },
  methods: {
    next() {
      if (this.visibleSlide >= this.slideLeng - 1) {
        this.visibleSlide = 0;
      } else {
        this.visibleSlide++;
      }
      this.direction = "left";
    },
    previous() {
      if (this.visibleSlide <= 0) {
        this.visibleSlide = this.slideLeng - 1;
      } else {
        this.visibleSlide--;
      }
      this.direction = "right";
    },
  },
};
</script>
